html {
  font: 16px/1.618 'Roboto', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  color: #525252; }

a {
  color: inherit; }
  a:hover {
    color: #252d3a; }

button, .button {
  height: 40px;
  line-height: 40px;
  display: inline-block;
  padding: 0 30px;
  border: 0;
  background-color: transparent;
  box-shadow: 0 0 0 1px #858585;
  border-radius: 3px;
  font-size: .875rem;
  font-weight: 400;
  color: #858585;
  text-decoration: none;
  transition: 150ms; }
  button:hover, .button:hover {
    box-shadow: 0 0 0 1px #252d3a;
    color: #252d3a; }

.button-group {
  display: flex; }
  .button-group button, .button-group .button {
    margin-left: 1px;
    border-radius: 0;
    position: relative; }
    .button-group button:hover, .button-group .button:hover {
      z-index: 1; }
    .button-group button:first-child, .button-group .button:first-child {
      margin-left: 0;
      border-radius: 3px 0px 0px 3px; }
    .button-group button:last-child, .button-group .button:last-child {
      border-radius: 0px 3px 3px 0px; }

table {
  width: calc(100% + 30px);
  margin: -30px -15px; }
  table tr th {
    height: 50px;
    padding: 0 15px;
    border-bottom: 1px solid #ebebeb;
    text-align: left;
    font-weight: 400; }
  table tr td {
    height: 50px;
    padding: 0 15px;
    border-bottom: 1px solid #ebebeb;
    font-weight: 300; }
  table tr:last-of-type td {
    border: none; }

.input {
  display: block;
  margin-bottom: 20px; }
  .input input {
    width: 100%;
    height: 40px;
    display: block;
    padding: 0 15px;
    box-sizing: border-box;
    border-radius: 3px;
    border: 1px solid #858585;
    font-weight: 300; }
  .input > div {
    font-weight: 300; }
  .input:last-child {
    margin-bottom: 0; }

html {
  height: 100%; }
  html body {
    height: 100%;
    overflow: hidden;
    background-color: #e6ebf0; }
    html body > div {
      width: calc(100% - 240px);
      height: 100%;
      float: left; }

body > nav {
  width: 240px;
  height: 100%;
  float: left;
  background-color: #252d3a; }
  body > nav > h1 {
    margin: 0;
    overflow: hidden;
    padding: 20px 30px;
    border-bottom: 1px solid #2f394a;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    color: #24a79f; }
  body > nav > ul {
    margin: 0;
    padding: 0; }
    body > nav > ul li a {
      width: 100%;
      height: 54px;
      display: block;
      padding: 0 30px;
      line-height: 54px;
      box-sizing: border-box;
      border-left: 3px solid #1b212a;
      border-bottom: 1px solid #2f394a;
      color: #737a85;
      text-decoration: none;
      transition: 150ms; }
      body > nav > ul li a:hover {
        color: #bcc6d5; }
      body > nav > ul li a.active {
        border-left-color: #24a79f;
        color: #dce0e8; }

body > div > header {
  height: 50px;
  position: relative;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  font-size: .5rem;
  z-index: 100; }

main {
  height: calc(100% - 50px);
  box-sizing: border-box;
  overflow: auto;
  padding: 30px; }
  main > header {
    height: 100px;
    display: flex;
    position: relative;
    margin: -30px -30px 30px -30px;
    padding: 0 30px;
    justify-content: space-between;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
    background-color: #ffffff;
    align-items: center;
    z-index: 1; }
    main > header > h1 {
      margin: 0;
      font-weight: 300;
      font-size: 1.875rem; }
  main .panel {
    padding: 30px;
    box-sizing: border-box;
    border-radius: 3px;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3);
    background-color: #ffffff; }
  main .flex {
    display: flex; }
  main .content {
    flex: 1 0 auto; }
    main .content > header {
      display: flex;
      justify-content: space-between;
      margin: -15px -15px 15px -15px;
      padding: 15px 15px 30px 15px;
      border-bottom: 1px solid #ebebeb; }
  main .activities {
    flex: 0 0 400px;
    margin-left: 30px; }
